import { API_URL, PAGE_SIZE } from '@/assets/config'
import { NoticeKeys, NoticeType } from '@/assets/constant'
import Cache from '@/assets/cache'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Service from '@/assets/service/service'
export default {
  name: 'department',
  data () {
    return {
      api: API_URL,
      code: 'department',
      dataKeys: NoticeKeys,
      notice: NoticeType.department,
      items: [],
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      condition: {},
      searchOptions: [
        { label: '标题', key: 'name' }
      ],
      operation: {}
    }
  },
  methods: {
    publicSearch (condition) {
      for (const key in condition) {
        this.condition[key] = condition[key]
      }
      this.getDataTables()
    },
    search () {
      this.getDataTables()
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    getDataTables (page = 1) {
      const request = {
        type: this.notice.type
      }
      if (page) {
        request['page'] = page
      }
      for (const key in this.condition) {
        if (this.condition[key]) {
          request[key] = this.condition[key]
        }
      }

      Service.Notice.get(request).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data
          this.total = response.data.total
          setTimeout(() => utilizeService.iCheck(), 200)
        }
      })
    },
    download (item) {
      this.fileExport({
        ids: [item.id],
        model: this.code
      })
    },
    fileExport (request) {
      Service.FileDownload(request).then(response => {
        dataService.download(response)
      })
    },
    delConfirm (item) {
      Service.Notice.del(item.id).then(response => {
        if (response.code == 200) {
          this.getDataTables()
        }
      })
    },
    toTop (item, option) {
      Service.Notice.edit(item.id, { top: option }).then(response => {
        if (response.code == 200) {
          toastr.success(response.msg)
          this.getDataTables()
        }
      })
    },
    add () {
      tabsService.addDetailsTab('/views/notice/details/new?type=' + this.notice.type, this.notice.name + '-新增')
    },
    see (item) {
      tabsService.addDetailsTab('/views/notice/details/' + item.id + '?type=' + this.notice.type + '&readonly=readonly', this.notice.name + '-详情-' + item.id)
    },
    edit (item) {
      tabsService.addDetailsTab('/views/notice/details/' + item.id + '?type=' + this.notice.type, this.notice.name + '-详情-' + item.id)
    }
  },
  created () {
    if (Cache.hasPermission('post_notification')) {
      this.operation.add = true
    }
    if (Cache.hasPermission('update_notification')) {
      this.operation.edit = true
    }
    if (Cache.hasPermission('delete_notification')) {
      this.operation.del = true
    }
    if (Cache.hasPermission('top_notification')) {
      this.operation.top = true
    }
    this.getDataTables()
  },
  mounted () {
  }
}



